import React, { forwardRef } from 'react';
import {
  Typography,
  Skeleton,
  Card,
  CardHeader,
  CardContent,
  useMediaQuery,
} from '@material-ui/core';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import { useWindowSize } from '../../hooks/useWindowSize';

const useStyles = makeStyles((theme) => ({
  cardContainer: {
    minWidth: theme.breakpoints.values.xxs,
    backgroundColor: theme.palette.mode === 'dark' ? grey[900] : grey[50],
    [theme.breakpoints.down('sm')]: { width: (width) => width },
    [theme.breakpoints.up('sm')]: { width: theme.breakpoints.values.sm },
  },
  cardMedia: {
    minWidth: theme.breakpoints.values.xxs,
    minHeight: theme.breakpoints.values.xxs / 1.75,
    [theme.breakpoints.down('sm')]: {
      width: (width) => width,
      height: (width) => width / 1.75,
    },
    [theme.breakpoints.up('sm')]: {
      width: theme.breakpoints.values.sm,
      height: theme.breakpoints.values.sm / 1.75,
    },
  },
}));

const CardSkeleton = forwardRef((props, ref) => {
  const { width, height } = useWindowSize();
  const classes = useStyles(width);

  return (
    <Card ref={ref} className={classes.cardContainer}>
      <CardHeader
        avatar={
          <Skeleton
            animation="wave"
            variant="circular"
            width={40}
            height={40}
          />
        }
        title={
          <Typography variant="subtitle1">
            <Skeleton />
          </Typography>
        }
        subheader={
          <Typography variant="subtitle2">
            <Skeleton />
          </Typography>
        }
      />
      <Skeleton
        animation="wave"
        variant="rectangular"
        className={classes.cardMedia}
      />
      <CardContent>
        <Typography variant="h6">
          <Skeleton />
        </Typography>
        <Typography variant="body1">
          <Skeleton />
        </Typography>
        <Typography variant="caption">
          <Skeleton width="80%" />
        </Typography>
      </CardContent>
    </Card>
  );
});

export default CardSkeleton;
