import React, { useState, useCallback, useRef, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Pagination, Box, Typography, useMediaQuery } from '@material-ui/core';

import CardMain from './CardMain';
import CardSkeleton from './CardSkeleton';

const useStyles = makeStyles((theme) => ({
  paginationStyle: {
    margin: `${theme.spacing(2)} 0`,
    [theme.breakpoints.down('sm')]: { maxWidth: 322 },
    [theme.breakpoints.up('sm')]: { maxWidth: 342 },
  },
  cardContainer: {
    [theme.breakpoints.up('sm')]: {
      marginTop: theme.spacing(3),
    },
  },
  card: {
    marginBottom: theme.spacing(4),
  },
}));

const PaginationWrapper = (props) => {
  const { pageNumber, currentPage, paginationHandler } = props;
  const theme = useTheme();
  const classes = useStyles();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Pagination
      boundaryCount={1}
      count={pageNumber}
      page={currentPage}
      hidePrevButton={isSmallDevice ? true : false}
      hideNextButton={isSmallDevice ? true : false}
      siblingCount={1}
      size={isSmallDevice ? 'large' : 'medium'}
      variant="text"
      onChange={paginationHandler}
      className={classes.paginationStyle}
    />
  );
};

const CardList = (props) => {
  const { cardDataList, isLoading = false } = props;

  const classes = useStyles();
  const [incrementalDataList, setIncrementalDataList] = useState([]);
  const [hasMoreCard, setHasMoreCard] = useState(false);
  // const [isLoading, setIsLoading] = useState(false);

  const [singlePageCardList, setSinglePageCardList] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [isLastPage, setIsLastPage] = useState(false);
  const pageSize = 8;

  useEffect(() => {
    if (cardDataList.length > 0) {
      setPageNumber(Math.ceil(cardDataList.length / pageSize));
    }
  }, [cardDataList]);

  useEffect(() => {
    if (cardDataList.length > 0) {
      setSinglePageCardList(
        cardDataList.slice(
          currentPage * pageSize - pageSize,
          Math.ceil(cardDataList.length / pageSize) === currentPage
            ? cardDataList.length
            : currentPage * pageSize
        )
      );
    }
  }, [currentPage, cardDataList]);

  const paginationHandler = (event, value) => {
    setCurrentPage(value);
  };

  useEffect(() => {
    setIncrementalDataList(singlePageCardList.slice(0, 1));
  }, [singlePageCardList]);

  useEffect(() => {
    setHasMoreCard(
      singlePageCardList.length !== incrementalDataList.length ? true : false
    );
  }, [singlePageCardList, incrementalDataList]);

  const observer = useRef();
  const lastOnListRef = useCallback(
    (node) => {
      if (isLoading) return;
      if (observer.current) observer.current.disconnect();

      const config = {
        threshold: [0, 0.25, 0.5, 0.75, 1],
      };

      observer.current = new IntersectionObserver((entries) => {
        if (entries[0].isIntersecting && hasMoreCard) {
          setIncrementalDataList((prev) => {
            return [...prev, singlePageCardList[prev.length]];
          });
        }
      }, config);

      if (node) observer.current.observe(node);
    },
    [isLoading, hasMoreCard, singlePageCardList]
  );

  return incrementalDataList.length > 0 ? (
    <Box
      sx={{
        display: 'grid',
        flexDirection: 'column',
        justifyContent: 'center',
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'center' }}>
        <PaginationWrapper
          pageNumber={pageNumber}
          currentPage={currentPage}
          paginationHandler={paginationHandler}
        />
      </Box>
      {incrementalDataList.map((cardData, index) => {
        return (
          <Box key={index} className={classes.card}>
            <CardMain cardData={cardData} cardIsExpanded={false} />
          </Box>
        );
      })}

      {hasMoreCard ? <CardSkeleton ref={lastOnListRef} /> : null}
      <Box sx={{ display: 'flex', justifyContent: 'center' }}>
        <PaginationWrapper
          pageNumber={pageNumber}
          currentPage={currentPage}
          paginationHandler={paginationHandler}
        />
      </Box>
    </Box>
  ) : (
    <Typography>No cards available.</Typography>
  );
};

export default CardList;
