import React from 'react';
import {
  Dialog,
  DialogContent,
  Button,
  useMediaQuery,
  SwipeableDrawer,
  Typography,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import { Link as RouterLink } from 'react-router-dom';

import CustomDialogTitle from './CustomDialogTitle';
import { useStore } from '../../hooks/store';

const CustomContent = (props) => {
  const { closeHandler, svgPath, title, shouldAuthenticated, children } = props;
  const [state, dispatch] = useStore();

  return (
    <>
      <CustomDialogTitle
        id="bookmark-dialog-title"
        onClose={closeHandler}
        svgIcon={svgPath}
      >
        {title}
      </CustomDialogTitle>
      <DialogContent dividers>
        {!shouldAuthenticated ? (
          children
        ) : state.isAuthenticated ? (
          children
        ) : (
          <Typography>
            Please{' '}
            <Button
              component={RouterLink}
              to={`/signin`}
              color="secondary"
              variant="contained"
              // onClick={addHandler}
            >
              Sign In
            </Button>{' '}
            in order to continue.
          </Typography>
        )}
      </DialogContent>
    </>
  );
};

const ResponsiveDialog = (props) => {
  const {
    isOpen,
    closeHandler,
    svgPath,
    title,
    shouldAuthenticated,
    children,
  } = props;
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  return isSmallDevice ? (
    <SwipeableDrawer
      anchor="bottom"
      onOpen={closeHandler}
      open={isOpen}
      onClose={closeHandler}
      disableDiscovery={true}
      disableSwipeToOpen={true}
    >
      <CustomContent
        closeHandler={closeHandler}
        svgPath={svgPath}
        title={title}
        shouldAuthenticated={shouldAuthenticated}
      >
        {children}
      </CustomContent>
    </SwipeableDrawer>
  ) : (
    <Dialog
      open={isOpen}
      aria-labelledby="dialog-title"
      onClose={closeHandler}
      fullWidth
      maxWidth="xs"
    >
      <CustomContent
        closeHandler={closeHandler}
        svgPath={svgPath}
        title={title}
        shouldAuthenticated={shouldAuthenticated}
      >
        {children}
      </CustomContent>
    </Dialog>
  );
};

export default ResponsiveDialog;
