import React from 'react';
import {
  IconButton,
  Link,
  SvgIcon,
  Tooltip,
  Zoom,
  Box,
  ButtonBase,
  Typography,
  DialogActions,
  DialogContent,
  TextField,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';
import { LoadingButton } from '@material-ui/lab';
import QRCode from 'react-qr-code';
import clsx from 'clsx';

import { useStore } from '../../hooks/store';
import {
  phoneIcon,
  messageIcon,
  emailIcon,
  qrCodeIcon,
  chevronDownIcon,
  bookmarkIcon,
  bookmarkMultipleIcon,
  folderAddIcon,
  plusIcon,
  backIcon,
} from '../../assets/svg/Common';
import ResponsiveDialog from '../HOC/ResponsiveDialog';
import CardHolder from '../Account/CardHolder';
import RestructureFormObject from '../HOC/RestructureFormObject';

const useStyles = makeStyles((theme) => ({
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  svgContainer: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
  },
}));

const CardActionLinks = (props) => {
  const {
    data,
    bookmarkIsOpen,
    shareIsOpen,
    qrCodeIsOpen,
    bookmarkHandler,
    shareHandler,
    qrCodeHandler,
    expanded,
    handleExpandClick,
    isCreationMode,
    creationModeHandler,
    addHandler,
    newFolderInputs,
    newFolderInputsChangeHandler,
  } = props;
  const [state, dispatch] = useStore();
  const classes = useStyles();

  const theme = useTheme();

  return (
    <>
      {/* <Link
        href={data.website}
        underline="none"
        target="_blank"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="website">
          <SvgIcon>
            <path d={earthIcon.path} />
          </SvgIcon>
        </IconButton>
      </Link> */}
      <Tooltip arrow TransitionComponent={Zoom} title="Bookmark">
        <IconButton aria-label="bookmark" onClick={bookmarkHandler}>
          <SvgIcon>
            <path d={bookmarkIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>

      <ResponsiveDialog
        isOpen={bookmarkIsOpen}
        closeHandler={bookmarkHandler}
        svgPath={bookmarkMultipleIcon.path}
        title={'Card Holders'}
        shouldAuthenticated={true}
      >
        {isCreationMode ? (
          <Box>
            <DialogContent>
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <Box sx={{ display: 'flex' }}>
                  <RestructureFormObject
                    formObject={newFolderInputs}
                    formChangeHandler={newFolderInputsChangeHandler}
                  />
                </Box>
              </Box>
            </DialogContent>
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                padding: '8px',
              }}
            >
              <Box
                sx={{
                  display: 'flex',
                  width: 48,
                  height: 48,
                }}
              >
                <Tooltip arrow TransitionComponent={Zoom} title="Back">
                  <IconButton aria-label="back" onClick={creationModeHandler}>
                    <SvgIcon>
                      <path d={backIcon.path} />
                    </SvgIcon>
                  </IconButton>
                </Tooltip>
              </Box>
              <Box sx={{ display: 'flex' }}>
                <LoadingButton
                  pendingPosition="start"
                  color="secondary"
                  variant="contained"
                  startIcon={
                    <SvgIcon>
                      <path d={plusIcon.path} />
                    </SvgIcon>
                  }
                  onClick={addHandler}
                >
                  Create
                </LoadingButton>
              </Box>
            </Box>
          </Box>
        ) : (
          <Box sx={{ display: 'flex' }}>
            <Box
              sx={{
                display: 'grid',
                [theme.breakpoints.down('xs')]: {
                  gridTemplateColumns: `120px 120px`,
                },
                [theme.breakpoints.up('xs')]: {
                  gridTemplateColumns: `120px 120px 120px`,
                },

                justifyContent: 'center',
                gridGap: `${theme.spacing(2)}`,
              }}
            >
              {state.user.cardHolders.map((cardHolder) => (
                <CardHolder
                  key={cardHolder.label}
                  cardHolder={cardHolder}
                  bookmarkHandler={addHandler}
                />
              ))}
              <Tooltip arrow TransitionComponent={Zoom} title="New Card Holder">
                <ButtonBase
                  focusRipple={true}
                  onClick={creationModeHandler}
                  className={classes.buttonContainer}
                >
                  <Box sx={{ display: 'grid' }}>
                    <SvgIcon className={classes.svgContainer}>
                      <path d={folderAddIcon.path} />
                    </SvgIcon>
                    <Typography
                      variant="caption"
                      noWrap={true}
                      gutterBottom={true}
                    >
                      New Card Holder
                    </Typography>
                  </Box>
                </ButtonBase>
              </Tooltip>
            </Box>
          </Box>
        )}
      </ResponsiveDialog>

      <Link
        href={`tel:+${data.contactPerson.phone}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Call">
          <IconButton aria-label="call">
            <SvgIcon>
              <path d={phoneIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`mailto:${data.contactPerson.email}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Email">
          <IconButton aria-label="email">
            <SvgIcon>
              <path d={emailIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`sms:/${data.contactPerson.phone}/?body=/${data.contactPerson.sms}/`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Message">
          <IconButton aria-label="message">
            <SvgIcon>
              <path d={messageIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Tooltip arrow TransitionComponent={Zoom} title="QR Code">
        <IconButton aria-label="qr-code" onClick={qrCodeHandler}>
          <SvgIcon>
            <path d={qrCodeIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>

      <ResponsiveDialog
        isOpen={qrCodeIsOpen}
        closeHandler={qrCodeHandler}
        svgPath={qrCodeIcon.path}
        title={'QR Code'}
        shouldAuthenticated={false}
      >
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'center',
          }}
        >
          <QRCode value={data.initLink} />
        </Box>
      </ResponsiveDialog>
      <IconButton
        className={clsx(classes.expand, {
          [classes.expandOpen]: expanded,
        })}
        onClick={handleExpandClick}
        aria-expanded={expanded}
        aria-label="show more"
      >
        <SvgIcon>
          <path d={chevronDownIcon.path} />
        </SvgIcon>
      </IconButton>
    </>
  );
};

export default CardActionLinks;
