import React, { useState, useEffect, useMemo, useCallback } from 'react';
import {
  Card,
  CardHeader,
  Popper,
  ClickAwayListener,
  CardMedia,
  CardContent,
  CardActions,
  Collapse,
  IconButton,
  Divider,
  Avatar,
  Paper,
  List,
  SvgIcon,
  useMediaQuery,
} from '@material-ui/core';
import {
  makeStyles,
  withStyles,
  styled,
  useTheme,
} from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';
import { Link as RouterLink } from 'react-router-dom';

import CardDisplayWrapper from './CardDisplayWrapper';
import CardMoreOptions from './CardMoreOptions';
import CardChips from './CardChips';
import CardLinks from './CardLinks';
import CardDescriptionList from './CardDescriptionList';
import CardActionLinks from './CardActionLinks';
import { useWindowSize } from '../../hooks/useWindowSize';
import {
  visibilityOffIcon,
  reportIcon,
  closeIcon,
  pencilIcon,
  trashIcon,
  dotsHorizontalIcon,
  dotsVerticalIcon,
  viewListIcon,
  closeCircleIcon,
} from '../../assets/svg/Common';
import { useStore } from '../../hooks/store';
import SnackbarAlert from '../HOC/SnackbarAlert';
import ResponsiveDialog from '../HOC/ResponsiveDialog';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import CardMoreOption from './CardMoreOption';

const useStyles = makeStyles((theme) => ({
  cardContainer: {
    minWidth: theme.breakpoints.values.xxs,
    backgroundColor: theme.palette.mode === 'dark' ? grey[900] : grey[50],
    [theme.breakpoints.down('sm')]: { width: (width) => width },
    [theme.breakpoints.up('sm')]: { width: theme.breakpoints.values.sm },
  },
  cardMedia: {
    minWidth: theme.breakpoints.values.xxs,
    minHeight: theme.breakpoints.values.xxs / 1.75,
    [theme.breakpoints.down('sm')]: {
      width: (width) => width,
      height: (width) => width / 1.75,
    },
    [theme.breakpoints.up('sm')]: {
      width: theme.breakpoints.values.sm,
      height: theme.breakpoints.values.sm / 1.75,
    },
  },
  cardChipContainer: {
    padding: '0',
  },
}));

const styles = (theme) => ({
  moreOptionsContainer: {},
});

const CustomPopper = styled(Popper)({
  position: 'absolute',
});

const MoreOptionsContainer = withStyles(styles)(
  React.forwardRef((props, ref) => {
    const { classes, children, ...rest } = props;
    return (
      <Paper className={classes.moreOptionsContainer} {...rest}>
        {children}
      </Paper>
    );
  })
);

const MoreOptionsOnClick = (props) => {
  const {
    moreOptionsToggleHandler,
    popperId,
    moreOptions,
    moreOptionsIsOpen,
    anchorEl,
    isCreationMode,
    creationModeHandler,
    addHandler,
  } = props;

  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const [state, dispatch] = useStore();

  return (
    <>
      <IconButton
        aria-describedby={popperId}
        onClick={moreOptionsToggleHandler}
        ref={anchorEl}
        disabled={moreOptionsIsOpen}
      >
        <SvgIcon>
          <path
            d={isSmallDevice ? dotsHorizontalIcon.path : dotsVerticalIcon.path}
          />
        </SvgIcon>
      </IconButton>

      {state.isAuthenticated && !isSmallDevice ? (
        <ClickAwayListener
          mouseEvent="onMouseDown"
          touchEvent="onTouchStart"
          onClickAway={moreOptionsToggleHandler}
        >
          <CustomPopper
            id={popperId}
            open={moreOptionsIsOpen}
            anchorEl={anchorEl}
            popperRef={anchorEl}
            placement="bottom-end"
            modifiers={[
              { name: 'flip', enabled: false },
              {
                name: 'preventOverflow',
                enabled: false,
              },
            ]}
          >
            <MoreOptionsContainer>
              <CardMoreOptions
                moreOptions={moreOptions}
                moreOptionsHandler={moreOptionsToggleHandler}
              />
            </MoreOptionsContainer>
          </CustomPopper>
        </ClickAwayListener>
      ) : (
        <ResponsiveDialog
          isOpen={moreOptionsIsOpen}
          closeHandler={moreOptionsToggleHandler}
          svgPath={viewListIcon.path}
          title={'More Options'}
          shouldAuthenticated={true}
        >
          <List>
            {moreOptions.map((action) => (
              <CardMoreOption key={action.id} linkItem={action} />
            ))}
          </List>
        </ResponsiveDialog>
      )}
    </>
  );
};

const CardMain = (props) => {
  const { cardData, cardIsExpanded } = props;
  const theme = useTheme();
  const [state, dispatch] = useStore(true);

  const deleteCardHandler = useCallback(() => {
    setSnackbarIsOpen(true);
    setMoreOptionsIsOpen(false);
    setAnchorEl(null);
  }, []);

  const unauthorizedOptions = useMemo(
    () => [
      {
        id: 'hide',
        label: 'Hide',
        description: 'Show Hide Options',
        actionHandler: deleteCardHandler,
        type: 'action',
        svgPath: visibilityOffIcon.path,
      },
      {
        id: 'block',
        label: 'Block',
        description: 'Block the selected person',
        actionHandler: deleteCardHandler,
        type: 'action',
        svgPath: closeIcon.path,
      },
      {
        id: 'report',
        label: 'Report',
        description: 'Show Report Options',
        actionHandler: deleteCardHandler,
        type: 'action',
        svgPath: reportIcon.path,
      },
    ],
    [deleteCardHandler]
  );

  const authorizedOptions = useMemo(
    () => [
      {
        id: 'edit',
        label: 'Edit',
        description: 'Edit the selected card',
        type: 'link',
        linkTo: 'card-details',
        cardData: cardData,
        svgPath: pencilIcon.path,
      },
      {
        id: 'delete',
        label: 'Delete',
        description: 'Delete the selected card',
        actionHandler: deleteCardHandler,
        type: 'action',
        svgPath: trashIcon.path,
      },
    ],
    [cardData]
  );
  const [bookmarkList, setBookmarkList] = useState([
    'Favorites',
    'Personal',
    'Business',
  ]);
  const { width, height } = useWindowSize();
  const classes = useStyles(width);

  const [newFolderInputs, setNewFolderInputs] = useState({
    newFolder: {
      value: '',
      name: 'new-folder',
      label: 'New Folder',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });
  const [moreOptions, setMoreOptions] = useState(unauthorizedOptions);
  const [cardDisplayType, setCardDisplayType] = useState('3d');
  const [expanded, setExpanded] = useState(cardIsExpanded);
  const [moreOptionsIsOpen, setMoreOptionsIsOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [bookmarkIsOpen, setBookmarkIsOpen] = useState(false);
  const [shareIsOpen, setShareIsOpen] = useState(false);
  const [qrCodeIsOpen, setQRCodeIsOpen] = useState(false);
  const [isCreationMode, setIsCreationMode] = useState(false);
  const [snackbarIsOpen, setSnackbarIsOpen] = useState(false);

  useEffect(() => {
    setMoreOptions(
      state.user._id === cardData.users._id
        ? authorizedOptions
        : unauthorizedOptions
    );
  }, [
    state.user._id,
    cardData.users._id,
    authorizedOptions,
    unauthorizedOptions,
  ]);

  const creationModeHandler = useCallback(() => {
    setIsCreationMode((prev) => !prev);
  }, []);

  const handleExpandClick = useCallback(() => {
    setExpanded((prev) => !prev);
  }, []);

  const moreOptionsToggleHandler = useCallback(
    (event) => {
      setMoreOptionsIsOpen((prev) => !prev);
      setAnchorEl(anchorEl ? null : event.currentTarget);
    },
    [anchorEl]
  );

  const bookmarkHandler = useCallback(() => {
    setBookmarkIsOpen((prev) => !prev);
    setIsCreationMode(false);
  }, []);

  const shareHandler = useCallback(() => {
    setShareIsOpen((prev) => !prev);
  }, []);

  const qrCodeHandler = useCallback(() => {
    setQRCodeIsOpen((prev) => !prev);
  }, []);

  const newFolderInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setNewFolderInputs(updatedForm);
  };

  const snackbarCloseHandler = () => {
    setSnackbarIsOpen(false);
  };

  const createNewBookmarkHandler = useCallback(() => {
    setBookmarkIsOpen(false);
    setSnackbarIsOpen(true);
    setIsCreationMode(false);
  }, []);

  const popperId = moreOptionsIsOpen ? 'more-options-popper' : undefined;

  if (cardData) {
    return (
      <>
        <Card className={classes.cardContainer}>
          <CardHeader
            title={cardData.name}
            subheader={cardData.companyName}
            avatar={
              <RouterLink to={'/account'}>
                <Avatar src={cardData.users.image} />
              </RouterLink>
            }
            action={
              <MoreOptionsOnClick
                moreOptions={moreOptions}
                popperId={popperId}
                moreOptionsToggleHandler={moreOptionsToggleHandler}
                moreOptionsIsOpen={moreOptionsIsOpen}
                anchorEl={anchorEl}
                isCreationMode={isCreationMode}
                creationModeHandler={creationModeHandler}
                addHandler={createNewBookmarkHandler}
              />
            }
          ></CardHeader>
          <CardMedia className={classes.cardMedia}>
            <CardDisplayWrapper
              data={cardData}
              cardDisplayType={cardDisplayType}
            />
          </CardMedia>
          <CardContent className={classes.cardChipContainer}>
            <CardChips data={cardData} className={classes.CardChipsContainer} />
          </CardContent>
          <Divider variant="middle" />
          <CardContent>
            <CardDescriptionList details={cardData.details} />
          </CardContent>
          <CardActions disableSpacing>
            <CardActionLinks
              data={cardData}
              bookmarkIsOpen={bookmarkIsOpen}
              shareIsOpen={shareIsOpen}
              qrCodeIsOpen={qrCodeIsOpen}
              bookmarkHandler={bookmarkHandler}
              shareHandler={shareHandler}
              qrCodeHandler={qrCodeHandler}
              expanded={expanded}
              handleExpandClick={handleExpandClick}
              bookmarkList={bookmarkList}
              isCreationMode={isCreationMode}
              creationModeHandler={creationModeHandler}
              addHandler={createNewBookmarkHandler}
              newFolderInputs={newFolderInputs}
              newFolderInputsChangeHandler={newFolderInputsChangeHandler}
            />
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <CardLinks data={cardData} />
            </CardContent>
          </Collapse>
        </Card>

        <SnackbarAlert
          isOpen={snackbarIsOpen}
          closeHandler={snackbarCloseHandler}
          severity={'info'}
        >
          The action request wil be sent to authentic server in production, this
          is only for demo purposes.
        </SnackbarAlert>
      </>
    );
  }
};

export default CardMain;
