import React from 'react';
import { Snackbar, Slide, Alert } from '@material-ui/core';

const SnackbarAlert = (props) => {
  const { isOpen, closeHandler, severity, children } = props;

  return (
    <Snackbar
      open={isOpen}
      onClose={closeHandler}
      TransitionComponent={Slide}
      autoHideDuration={3000}
    >
      <Alert onClose={closeHandler} variant="filled" severity={severity}>
        {children}
      </Alert>
    </Snackbar>
  );
};

export default SnackbarAlert;
