import React from 'react';
import {
  Grid,
  TextField,
  SvgIcon,
  InputAdornment,
  IconButton,
  Autocomplete,
  Chip,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ToggleIcon from 'material-ui-toggle-icon';
import { checkIcon, alertIcon } from '../../assets/svg/Common';

const useStyles = makeStyles((theme) => ({
  stepperInputContainer: {
    [theme.breakpoints.up('sm')]: {
      margin: `0 ${theme.spacing(1)}`,
    },
  },
}));

const HelperText = (props) => {
  const { isTouched, validity } = props;

  return isTouched && validity.isValid ? (
    <span>
      <SvgIcon
        style={{
          width: 12,
          height: 12,
          marginRight: 8,
        }}
      >
        <path d={checkIcon.path} />
      </SvgIcon>
      {validity.label}
    </span>
  ) : isTouched && !validity.isValid ? (
    <span>
      <SvgIcon
        style={{
          width: 12,
          height: 12,
          marginRight: 8,
        }}
      >
        <path d={alertIcon.path} />
      </SvgIcon>
      {validity.label}
    </span>
  ) : (
    validity.label
  );
};

const CustomInput = (props) => {
  const {
    id,
    fieldName,
    config,
    formObject,
    formChangeHandler,
    objectList,
    index,
  } = props;

  return (
    <TextField
      key={id}
      name={id}
      id={id}
      label={config.label}
      variant={config.variant}
      color="secondary"
      margin="normal"
      type={config.type}
      required={config.validations.isRequired}
      error={config.isTouched && !config.validity.isValid}
      value={config.value}
      multiline={config.multiline}
      maxRows={4}
      minRows={4}
      disabled={config.disabled}
      readOnly={config.disabled}
      style={{ width: 280 }}
      inputProps={{ 'data-index': index, maxLength: config.maxLength }}
      onChange={formChangeHandler({
        fieldName,
        formObject,
        objectList,
        index,
        changeType: 'change',
      })}
      onBlur={formChangeHandler({
        fieldName,
        formObject,
        objectList,
        index,
        changeType: 'change',
      })}
      helperText={
        <HelperText isTouched={config.isTouched} validity={config.validity} />
      }
      InputProps={{
        endAdornment: (
          <InputAdornment>
            {config.icon.type === 'toggle' ? (
              <IconButton
                disabled={Boolean(!config.value) || config.disabled}
                onClick={formChangeHandler({
                  fieldName,
                  formObject,
                  objectList,
                  index,
                  toggleType: 'passwordText',
                })}
              >
                <ToggleIcon
                  on={config.icon.isOn}
                  onIcon={
                    <SvgIcon>
                      <path d={config.icon.on.path} />
                    </SvgIcon>
                  }
                  offIcon={
                    <SvgIcon>
                      <path d={config.icon.off.path} />
                    </SvgIcon>
                  }
                />
              </IconButton>
            ) : (
              <IconButton
                disabled={Boolean(!config.value) || config.disabled}
                onClick={formChangeHandler({
                  fieldName,
                  formObject,
                  objectList,
                  index,
                  changeType: 'reset',
                })}
              >
                <SvgIcon>
                  <path d={config.icon.close.path} />
                </SvgIcon>
              </IconButton>
            )}
          </InputAdornment>
        ),
      }}
    />
  );
};

const CustomAutoComplete = (props) => {
  const {
    id,
    fieldName,
    config,
    formObject,
    formChangeHandler,
    objectList,
    index,
  } = props;

  return (
    <Autocomplete
      key={id}
      id={id}
      value={config.value}
      options={config.options.map((option) => option)}
      renderTags={(value, getTagProps) =>
        value.map((option, index) => (
          <Chip variant="filled" label={option} {...getTagProps({ index })} />
        ))
      }
      onChange={formChangeHandler({
        fieldName,
        formObject,
        objectList,
        index,
        changeType: 'change',
      })}
      renderInput={(params) => (
        <TextField
          {...params}
          variant="outlined"
          color="secondary"
          margin="normal"
          type={config.type}
          required={config.validations.isRequired}
          error={config.isTouched && !config.validity.isValid}
          value={config.value}
          multiline={config.multiline}
          maxRows={4}
          minRows={4}
          disabled={config.disabled}
          readOnly={config.disabled}
          style={{ width: 280 }}
          label={config.label}
          helperText={
            <HelperText
              isTouched={config.isTouched}
              validity={config.validity}
            />
          }
        />
      )}
      multiple={config.multiple}
      freeSolo={config.freeSolo}
      clearIcon={
        <SvgIcon>
          <path d={config.icon.close.path} />
        </SvgIcon>
      }
    />
  );
};

const InputTextRenderer = (props) => {
  const classes = useStyles();

  const { formList } = props;

  return formList.map((formItem) => {
    const {
      id,
      fieldName,
      config,
      formObject,
      formChangeHandler,
      objectList,
      index,
    } = formItem;

    return (
      <Grid item key={id} className={classes.stepperInputContainer}>
        {config.componentType ? (
          <CustomAutoComplete
            id={id}
            fieldName={fieldName}
            config={config}
            formObject={formObject}
            formChangeHandler={formChangeHandler}
            objectList={objectList}
            index={index}
          />
        ) : (
          <CustomInput
            id={id}
            fieldName={fieldName}
            config={config}
            formObject={formObject}
            formChangeHandler={formChangeHandler}
            objectList={objectList}
            index={index}
          />
        )}
      </Grid>
    );
  });
};

export default InputTextRenderer;
