import {
  Box,
  SvgIcon,
  ButtonBase,
  Typography,
  Tooltip,
  Zoom,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import { folderAccountIcon } from '../../assets/svg/Common';

const useStyles = makeStyles((theme) => ({
  svgContainer: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
  },
  buttonContainer: { borderRadius: theme.spacing(0.5) },
}));

const CardHolder = (props) => {
  const { cardHolder, bookmarkHandler } = props;
  const classes = useStyles();

  return (
    <Tooltip
      arrow
      TransitionComponent={Zoom}
      title={cardHolder.label}
      key={cardHolder.label}
    >
      <ButtonBase
        focusRipple={true}
        onClick={bookmarkHandler}
        className={classes.buttonContainer}
      >
        <Box sx={{ display: 'grid' }}>
          <SvgIcon className={classes.svgContainer}>
            <path d={folderAccountIcon.path} />
          </SvgIcon>
          <Typography variant="caption" noWrap={true} gutterBottom={true}>
            {cardHolder.label}
          </Typography>
        </Box>
      </ButtonBase>
    </Tooltip>
  );
};

export default CardHolder;
