import React from 'react';
import {
  IconButton,
  DialogTitle,
  Typography,
  SvgIcon,
  Box,
  Tooltip,
  Zoom,
  useMediaQuery,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import { closeIcon, plusIcon } from '../../assets/svg/Common';

const useStyles = makeStyles((theme) => ({
  dialogCloseButton: {
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  dialogAddButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
}));

const CustomDialogTitle = (props) => {
  const {
    children,
    svgIcon,
    onClose,
    onAdd,
    // isCreationMode,
    // toggleModeHandler,
    ...rest
  } = props;
  const classes = useStyles();
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  const TitleAction = () => {
    return onAdd ? (
      <Tooltip arrow TransitionComponent={Zoom} title="Add">
        <IconButton
          aria-label="add"
          onClick={onAdd}
          className={classes.dialogAddButton}
        >
          <SvgIcon>
            <path d={plusIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>
    ) : onClose ? (
      <Tooltip arrow TransitionComponent={Zoom} title="Close">
        <IconButton
          aria-label="close"
          onClick={onClose}
          // className={classes.dialogCloseButton}
        >
          <SvgIcon>
            <path d={closeIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>
    ) : null;
  };

  return (
    <DialogTitle disableTypography {...rest}>
      <Box
        sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}
      >
        <Box
          sx={{
            display: 'grid',
            [theme.breakpoints.down('sm')]: {
              justifyContent: 'center',
            },
            [theme.breakpoints.up('sm')]: {
              flexGrow: 1,
            },
            gridTemplateColumns: `24px auto`,
            gridGap: `${theme.spacing(2)}`,
            alignItems: 'center',
          }}
        >
          {isSmallDevice ? (
            <Box
              sx={{
                width: 30,
                height: 6,
                bgcolor:
                  theme.palette.mode === 'dark' ? 'grey.300' : 'grey.400',
                borderRadius: '3px',
                gridColumnStart: 1,
                gridColumnEnd: 3,
                margin: 'auto',
              }}
            />
          ) : null}
          <Box
            sx={{
              width: 24,
              height: 24,
              color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
            }}
          >
            <SvgIcon>
              <path d={svgIcon} />
            </SvgIcon>
          </Box>
          <Box sx={{}}>
            <Typography variant="h6" component="div">
              {children}
            </Typography>
          </Box>
        </Box>
        {isSmallDevice ? null : (
          <Box>
            <TitleAction />
          </Box>
        )}
      </Box>
    </DialogTitle>
  );
};

export default CustomDialogTitle;
