import React from 'react';
import {
  Box,
  SvgIcon,
  ButtonBase,
  Typography,
  Tooltip,
  Zoom,
} from '@material-ui/core';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

import PaperContainer from '../HOC/PaperContainer';
import { folderAddIcon } from '../../assets/svg/Common';
import { useStore } from '../../hooks/store';
import CardHolder from './CardHolder';

const useStyles = makeStyles((theme) => ({
  svgContainer: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
  },
  buttonContainer: { borderRadius: theme.spacing(0.5) },
}));

const CardHolders = (props) => {
  const theme = useTheme();
  const classes = useStyles();
  const [state, dispatch] = useStore();
  const bookmarkHandler = () => {};
  const newBookmarkHandler = () => {};

  return (
    <PaperContainer>
      <Box
        sx={{
          display: 'grid',
          [theme.breakpoints.down('xs')]: {
            gridTemplateColumns: `120px 120px`,
          },
          [theme.breakpoints.between('xs', 'sm')]: {
            gridTemplateColumns: `120px 120px 120px`,
          },
          [theme.breakpoints.between('sm', 'lg')]: {
            gridTemplateColumns: `120px 120px 120px 120px`,
          },
          [theme.breakpoints.up('lg')]: {
            gridTemplateColumns: `120px 120px 120px 120px 120px`,
          },
          justifyContent: 'center',
          gridGap: `${theme.spacing(2)}`,
        }}
      >
        {state.user.cardHolders.map((cardHolder) => (
          <CardHolder
            key={cardHolder.label}
            cardHolder={cardHolder}
            bookmarkHandler={bookmarkHandler}
          />
        ))}

        <Tooltip arrow TransitionComponent={Zoom} title="New Card Holder">
          <ButtonBase
            focusRipple={true}
            onClick={newBookmarkHandler}
            className={classes.buttonContainer}
          >
            <Box sx={{ display: 'grid' }}>
              <SvgIcon className={classes.svgContainer}>
                <path d={folderAddIcon.path} />
              </SvgIcon>
              <Typography variant="caption" noWrap={true} gutterBottom={true}>
                New Card Holder
              </Typography>
            </Box>
          </ButtonBase>
        </Tooltip>
      </Box>
    </PaperContainer>
  );
};

export default CardHolders;
