import React, { useState, useEffect } from 'react';
import { SvgIcon, Typography, Box } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Redirect, Link as RouterLink } from 'react-router-dom';
import { grey } from '@material-ui/core/colors';

import PaperContainer from '../HOC/PaperContainer';
import {
  earthIcon,
  linkIcon,
  linkLockIcon,
  shoppingCartPlusIcon,
  shoppingCartMinusIcon,
  creditCardIcon,
  shoppingCardRemoveIcon,
} from '../../assets/svg/Common';
import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({
  svgContainer: {
    width: theme.spacing(15),
    height: theme.spacing(15),
    color: theme.palette.mode === 'dark' ? grey[300] : grey[700],
  },
  privateCardContainer: {
    [theme.breakpoints.between('sm', 'lg')]: {
      gridColumnStart: 1,
      gridColumnEnd: 3,
    },
  },
}));

const Cart = () => {
  const classes = useStyles();
  const theme = useTheme();
  const [state, dispatch] = useStore();

  const products = [
    {
      name: 'Public Card',
      label: 'publicCard',
      price: 6,
      svgPath: earthIcon.path,
    },
    {
      name: 'Unlisted Card',
      label: 'unlistedCard',
      price: 5,
      svgPath: linkIcon.path,
    },
    {
      name: 'Private Card',
      label: 'privateCard',
      price: 10,
      svgPath: linkLockIcon.path,
    },
  ];

  const [publicCardNumber, setPublicCardNumber] = useState(0);
  const [unlistedCardNumber, setUnlistedCardNumber] = useState(0);
  const [privateCardNumber, setPrivateCardNumber] = useState(0);

  const [publicCardPlusIsDisabled, setPublicCardPlusIsDisabled] = useState(
    false
  );
  const [unlistedCardPlusIsDisabled, setUnlistedCardPlusIsDisabled] = useState(
    false
  );
  const [privateCardPlusIsDisabled, setPrivateCardPlusIsDisabled] = useState(
    false
  );
  const [publicCardMinusIsDisabled, setPublicCardMinusIsDisabled] = useState(
    true
  );
  const [
    unlistedCardMinusIsDisabled,
    setUnlistedCardMinusIsDisabled,
  ] = useState(true);
  const [privateCardMinusIsDisabled, setPrivateCardMinusIsDisabled] = useState(
    true
  );

  const [hasItemInCart, setHasItemInCart] = useState(false);

  const [totalAmount, setTotalAmount] = useState(0);

  const clearCartHandler = () => {
    setPublicCardNumber(0);
    setUnlistedCardNumber(0);
    setPrivateCardNumber(0);
    setTotalAmount(0);
  };

  useEffect(() => {
    setHasItemInCart(
      !!(publicCardNumber + unlistedCardNumber + privateCardNumber)
    );
  }, [publicCardNumber, unlistedCardNumber, privateCardNumber]);

  useEffect(() => {
    setPublicCardPlusIsDisabled(publicCardNumber >= 9 ? true : false);
    setPublicCardMinusIsDisabled(publicCardNumber <= 0 ? true : false);
  }, [publicCardNumber]);

  useEffect(() => {
    setUnlistedCardPlusIsDisabled(unlistedCardNumber >= 9 ? true : false);
    setUnlistedCardMinusIsDisabled(unlistedCardNumber <= 0 ? true : false);
  }, [unlistedCardNumber]);

  useEffect(() => {
    setPrivateCardPlusIsDisabled(privateCardNumber >= 9 ? true : false);
    setPrivateCardMinusIsDisabled(privateCardNumber <= 0 ? true : false);
  }, [privateCardNumber]);

  const plusHandler = (cardType, price) => () => {
    switch (cardType) {
      case 'publicCard':
        setPublicCardNumber((prev) => prev + 1);
        setTotalAmount((prev) => prev + price);
        break;
      case 'unlistedCard':
        setUnlistedCardNumber((prev) => prev + 1);
        setTotalAmount((prev) => prev + price);
        break;
      case 'privateCard':
        setPrivateCardNumber((prev) => prev + 1);
        setTotalAmount((prev) => prev + price);
        break;
      default:
        return 0;
    }
  };

  const minusHandler = (cardType, price) => () => {
    switch (cardType) {
      case 'publicCard':
        setPublicCardNumber((prev) => (prev > 0 ? prev - 1 : prev));
        setTotalAmount((prev) => prev - price);
        break;
      case 'unlistedCard':
        setUnlistedCardNumber((prev) => (prev > 0 ? prev - 1 : prev));
        setTotalAmount((prev) => prev - price);
        break;
      case 'privateCard':
        setPrivateCardNumber((prev) => (prev > 0 ? prev - 1 : prev));
        setTotalAmount((prev) => prev - price);
        break;
      default:
        return 0;
    }
  };

  if (!state.isAuthenticated) {
    return <Redirect to="/signin" />;
  }

  return (
    <PaperContainer>
      <Box
        sx={{
          display: 'grid',
          justifyContent: 'center',
          [theme.breakpoints.down('sm')]: {
            gridTemplateColumns: `${theme.spacing(28)}`,
          },
          [theme.breakpoints.between('sm', 'lg')]: {
            gridTemplateColumns: `${theme.spacing(28)} ${theme.spacing(28)}`,
          },
          [theme.breakpoints.up('lg')]: {
            gridTemplateColumns: `${theme.spacing(28)} ${theme.spacing(
              28
            )}  ${theme.spacing(28)}`,
          },
          gridGap: `${theme.spacing(2)}`,
        }}
      >
        {products.map((item) => (
          <Box
            key={item.label}
            sx={{
              backgroundColor:
                theme.palette.mode === 'dark' ? grey[700] : grey[50],
              borderRadius: '4px',
              display: 'grid',
              justifyContent: 'center',
              padding: '16px',
            }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'center' }}>
              <SvgIcon className={classes.svgContainer}>
                <path d={item.svgPath} />
              </SvgIcon>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'center' }}>
              <Typography variant="subtitle1" noWrap={true} gutterBottom={true}>
                {item.name}
              </Typography>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'center' }}>
              <Typography variant="subtitle1" noWrap={true} gutterBottom={true}>
                $ {item.price}
              </Typography>
            </Box>
            <Box
              sx={{
                display: 'grid',
                justifyContent: 'center',
                gridTemplateColumns: `64px 64px 64px`,
              }}
            >
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <LoadingButton
                  startIcon={
                    <SvgIcon>
                      <path d={shoppingCartPlusIcon.path} />
                    </SvgIcon>
                  }
                  color="secondary"
                  onClick={plusHandler(item.label, item.price)}
                  variant="contained"
                  className={classes.buttonContainer}
                  disabled={
                    item.label === 'publicCard'
                      ? publicCardPlusIsDisabled
                      : item.label === 'unlistedCard'
                      ? unlistedCardPlusIsDisabled
                      : privateCardPlusIsDisabled
                  }
                  // pending={profileIsLoading}
                />
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <Typography
                  variant="subtitle1"
                  noWrap={true}
                  gutterBottom={true}
                >
                  {item.label === 'publicCard'
                    ? publicCardNumber
                    : item.label === 'unlistedCard'
                    ? unlistedCardNumber
                    : privateCardNumber}
                </Typography>
              </Box>
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <LoadingButton
                  endIcon={
                    <SvgIcon>
                      <path d={shoppingCartMinusIcon.path} />
                    </SvgIcon>
                  }
                  color="secondary"
                  onClick={minusHandler(item.label, item.price)}
                  variant="contained"
                  className={classes.buttonContainer}
                  disabled={
                    item.label === 'publicCard'
                      ? publicCardMinusIsDisabled
                      : item.label === 'unlistedCard'
                      ? unlistedCardMinusIsDisabled
                      : privateCardMinusIsDisabled
                  }
                  // pending={profileIsLoading}
                />
              </Box>
            </Box>
          </Box>
        ))}
      </Box>

      <Box
        sx={{
          display: 'grid',
          justifyContent: 'center',
          gridGap: `${theme.spacing(2)}`,
        }}
        style={{ marginTop: theme.spacing(5) }}
      >
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <Typography variant="h6" noWrap={true} gutterBottom={true}>
            {`Total Amount: $ ${totalAmount}`}
          </Typography>
        </Box>

        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <LoadingButton
            endIcon={
              <SvgIcon>
                <path d={shoppingCardRemoveIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            onClick={clearCartHandler}
            variant="contained"
            className={classes.buttonContainer}
            disabled={!hasItemInCart}
            // pending={profileIsLoading}
          >
            Clear Cart
          </LoadingButton>
        </Box>

        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <LoadingButton
            component={RouterLink}
            to={`/checkout`}
            endIcon={
              <SvgIcon>
                <path d={creditCardIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            variant="contained"
            className={classes.buttonContainer}
            disabled={!hasItemInCart}
            // pending={profileIsLoading}
          >
            Checkout
          </LoadingButton>
        </Box>
      </Box>
    </PaperContainer>
  );
};

export default Cart;
