import React, { useState, useEffect, useCallback } from 'react';
import PaperContainer from '../HOC/PaperContainer';
import RestructureFormObject from '../HOC/RestructureFormObject';
import { SvgIcon, Box } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';
import { matchSorter } from 'match-sorter';
import { useParams } from 'react-router-dom';

import { closeCircleIcon, searchIcon } from '../../assets/svg/Common';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import CardList from '../Card/CardList';
import { FullList } from '../Demo/FullList';

const SearchPage = () => {
  let { forwardedSearchValue } = useParams();
  const initSearchKeyConfig = {
    keys: [
      // 'initLink',
      // 'companyName',
      // 'role',
      // (list) => list.links.map((link) => link.title),
      // (list) => list.links.map((li         nk) => link.link),
      'tags',
      // 'categories',
      // 'address',
      // (list) => list.details.map((detail) => detail.title),
      // (list) => list.details.map((detail) => detail.description),
      // 'address',
      // 'contactPerson.name',
      // 'contactPerson.phone',
      // 'contactPerson.email',
    ],
  };

  const [searchForm, setSearchForm] = useState({
    search: {
      value: forwardedSearchValue ? forwardedSearchValue : '',
      name: 'search',
      label: 'Search',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      multiline: false,
      disabled: false,
      validations: {
        isRequired: false,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    // tags: {
    //   value: forwardedSearchTag ? [forwardedSearchTag] : [],
    //   name: 'tags',
    //   label: 'Tags',
    //   type: 'select',
    //   dataType: 'array',
    //   componentType: 'Autocomplete',
    //   placeHolder: '',
    //   multiple: true,
    //   freeSolo: true,
    //   options: ['option1', 'option2', 'option3'],
    //   disabled: false,
    //   validations: {
    //     isRequired: false,
    //   },
    //   isTouched: false,
    //   validity: { isValid: true, label: 'optional' },
    //   icon: {
    //     type: 'close',
    //     close: closeCircleIcon,
    //   },
    // },
    // category: {
    //   value: '',
    //   name: 'category',
    //   label: 'Category',
    //   type: 'text',
    //   dataType: 'number',
    //   componentType: 'Autocomplete',
    //   placeHolder: '',
    //   multiple: false,
    //   freeSolo: false,
    //   options: ['', 'option1', 'option2', 'option3'],
    //   disabled: false,
    //   validations: {
    //     isRequired: false,
    //   },
    //   isTouched: false,
    //   validity: { isValid: true, label: 'optional' },
    //   icon: {
    //     type: 'close',
    //     close: closeCircleIcon,
    //   },
    // },
  });
  const [searchKeyConfig, setSearchKeyConfig] = useState(initSearchKeyConfig);
  const [searchKey, setSearchKey] = useState('');
  const [searchFormIsValid, setSearchFormIsValid] = useState(false);
  const [searchList, setSearchList] = useState(FullList);
  const [resultList, setResultList] = useState([]);

  // useEffect(() => {
  //   // production will send an api
  //   setSearchList(FullList);
  // }, []);

  const searchInputChangeHandler = useCallback(
    (args) => (event, newValue) => {
      const updatedForm = fieldChangeHandler(args, event, newValue);

      setSearchForm(updatedForm);
    },
    []
  );

  const searchFormCompleteChangeHandler = useCallback(
    (args) => {
      const { formObject } = args;

      const updatedForm = { ...formObject };

      const formValidityList = [];

      for (let key in updatedForm) {
        formValidityList.push(updatedForm[key].validity.isValid);
      }

      let formIsValid = true;
      formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

      if (updatedForm.search.value) {
        const searchResult = matchSorter(
          searchList,
          updatedForm.search.value,
          searchKeyConfig
        );
        setResultList(searchResult);
      } else {
        setResultList([]);
      }

      setSearchKey(updatedForm.search.value);
      setSearchFormIsValid(formIsValid);
    },
    [searchList, searchKeyConfig]
  );

  useEffect(() => {
    searchFormCompleteChangeHandler({ formObject: searchForm });
  }, [searchForm, searchFormCompleteChangeHandler]);

  return (
    <>
      <PaperContainer>
        <Box
          sx={{
            display: 'grid',
            direction: 'column',
            justifyContent: 'center',
          }}
        >
          <RestructureFormObject
            formObject={searchForm}
            formChangeHandler={searchInputChangeHandler}
          />
          {/* <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={searchIcon.path} />
                </SvgIcon>
              }
              color="secondary"
              // onClick={searchHandler}
              pendingPosition="start"
              variant="contained"
              // pending={profileIsLoading}
              disabled={!searchFormIsValid}
            >
              Search
            </LoadingButton>
          </Box> */}
        </Box>
      </PaperContainer>

      {resultList.length > 0 ? <CardList cardDataList={resultList} /> : null}
    </>
  );
};

export default SearchPage;
