import React, { useState, useCallback, useEffect } from 'react';
import { SvgIcon, Grid, Typography } from '@material-ui/core';
import LoadingButton from '@material-ui/lab/LoadingButton';
import { makeStyles } from '@material-ui/core/styles';
import { Link as RouterLink, Redirect } from 'react-router-dom';

import {
  closeCircleIcon,
  visibilityOffIcon,
  visibilityOnIcon,
  loginIcon,
  lockQuestionIcon,
  swapIcon,
} from '../../assets/svg/Common';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import { fieldsIsValidHandler } from '../helper/fieldsIsValidHandler';
import RestructureFormObject from '../HOC/RestructureFormObject';
import { useStore } from '../../hooks/store';
import { User } from '../Demo/User';
import PaperContainer from '../HOC/PaperContainer';
import SnackbarAlert from '../HOC/SnackbarAlert';

const useStyles = makeStyles((theme) => ({
  buttonSpacing: {
    marginTop: theme.spacing(1),
  },
}));

const Registration = (props) => {
  const [state, dispatch] = useStore(true);
  const [isPendingSignIn, setIsPendingSignIn] = useState(false);
  const [isPendingRequestPassword, setIsPendingRequestPassword] = useState(
    false
  );
  const [isPendingChangeType, setIsPendingChangeType] = useState(false);
  const [accountInputs, setAccountInputs] = useState({
    email: {
      value: '',
      name: 'email',
      label: 'Email',
      type: 'email',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
        isEmail: true,
      },
      isTouched: false,
      // validity: { isValid: false, label: '*Required' },
      validity: { isValid: false, label: '*test@test.com' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    password: {
      value: '',
      name: 'password',
      label: 'Password',
      type: 'password',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      // validity: { isValid: false, label: '*Required' },
      validity: { isValid: false, label: '*test' },
      icon: {
        type: 'toggle',
        on: visibilityOnIcon,
        off: visibilityOffIcon,
        isOn: false,
      },
    },
  });
  const [accountInputsIsValid, setAccountInputsIsValid] = useState(
    false
  );
  const [loginFailedSnackbarIsOpen, setLoginFailedSnackbarIsOpen] = useState(
    false
  );
  const [
    forgotPasswordSnackbarIsOpen,
    setForgotPasswordSnackbarIsOpen,
  ] = useState(false);

  const classes = useStyles();

  const signInHandler = () => {
    if (
      accountInputs.email.value === 'test@test.com' &&
      accountInputs.password.value === 'test'
    ) {
      dispatch('SIGN_IN', User);
    } else {
      setLoginFailedSnackbarIsOpen(true);
    }
  };

  const forgetPasswordHandler = () => {
    setForgotPasswordSnackbarIsOpen(true);
  };

  const accountInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setAccountInputs(updatedForm);
  };

  const accountInputsIsValidHandler = useCallback((args) => {
    const fieldsIsValid = fieldsIsValidHandler(args);

    setAccountInputsIsValid(fieldsIsValid);
  }, []);

  useEffect(() => {
    accountInputsIsValidHandler({ formObject: accountInputs });
  }, [accountInputs, accountInputsIsValidHandler]);

  const loginFailedSnackbarCloseHandler = () => {
    setLoginFailedSnackbarIsOpen(false);
  };

  const forgotPasswordSnackbarCloseHandler = () => {
    setForgotPasswordSnackbarIsOpen(false);
  };

  if (state.isAuthenticated) {
    return <Redirect to="/" />;
  }

  return (
    <PaperContainer>
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
      >
        <Typography variant="h4">Sign In</Typography>
        <Grid item>
          <RestructureFormObject
            formObject={accountInputs}
            formChangeHandler={accountInputsChangeHandler}
          />
        </Grid>

        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={loginIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            onClick={signInHandler}
            pending={isPendingSignIn}
            disabled={!accountInputsIsValid}
            pendingPosition="start"
            variant="contained"
            className={classes.buttonSpacing}
            type="submit"
          >
            Sign In
          </LoadingButton>
        </Grid>
        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            component={RouterLink}
            to={'/signup'}
            startIcon={
              <SvgIcon>
                <path d={swapIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            pending={isPendingChangeType}
            pendingPosition="start"
            variant="outlined"
            className={classes.buttonSpacing}
          >
            Switch to Register
          </LoadingButton>
        </Grid>

        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={lockQuestionIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            onClick={forgetPasswordHandler}
            pending={isPendingRequestPassword}
            pendingPosition="start"
            variant="outlined"
            className={classes.buttonSpacing}
          >
            Forget Password
          </LoadingButton>
        </Grid>
      </Grid>
      <SnackbarAlert
        isOpen={loginFailedSnackbarIsOpen}
        closeHandler={loginFailedSnackbarCloseHandler}
        severity={'info'}
      >
        Please use the test ID to sign in.
      </SnackbarAlert>
      <SnackbarAlert
        isOpen={forgotPasswordSnackbarIsOpen}
        closeHandler={forgotPasswordSnackbarCloseHandler}
        severity={'info'}
      >
        The password request wil be sent to authentic server in production, this
        is only for demo purposes.
      </SnackbarAlert>
    </PaperContainer>
  );
};

export default Registration;
