import React, { useState, useEffect, useCallback } from 'react';
import {
  CardElement,
  useStripe,
  useElements,
  Elements,
} from '@stripe/react-stripe-js';
import { Paper, SvgIcon, Box } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';
import { LoadingButton } from '@material-ui/lab';
import { Redirect } from 'react-router-dom';

import PaperContainer from '../HOC/PaperContainer';
import { closeCircleIcon, shoppingIcon } from '../../assets/svg/Common';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import { fieldsIsValidHandler } from '../helper/fieldsIsValidHandler';
import RestructureFormObject from '../HOC/RestructureFormObject';
import SnackbarAlert from '../HOC/SnackbarAlert';
import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({
  CardElementContainer: {
    width: 280,
    padding: theme.spacing(1),
    margin: theme.spacing(2),
    backgroundColor: theme.palette.mode === 'dark' ? grey[700] : grey[50],
  },
}));

const Checkout = () => {
  const classes = useStyles();
  const theme = useTheme();
  const stripe = useStripe();
  const elements = useElements();
  const [state, dispatch] = useStore();

  const [snackbarIsOpen, setSnackbarIsOpen] = useState(false);
  const [billingDetailInputsIsValid, setBillingDetailInputsIsValid] = useState(
    false
  );
  const [isProcessing, setIsProcessing] = useState(false);
  const [billingDetailInputs, setBillingDetailInputs] = useState({
    name: {
      value: '',
      name: 'name',
      label: 'Name',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    email: {
      value: '',
      name: 'email',
      label: 'Email',
      type: 'email',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    city: {
      value: '',
      name: 'city',
      label: 'City',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    streetAddress: {
      value: '',
      name: 'street-address',
      label: 'Street Address',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: true,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    state: {
      value: '',
      name: 'state',
      label: 'State',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    postal_code: {
      value: '',
      name: 'postal_code',
      label: 'Postal Code',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
  });

  const billingDetailsIsValidHandler = useCallback((args) => {
    const fieldsIsValid = fieldsIsValidHandler(args);

    setBillingDetailInputsIsValid(fieldsIsValid);
  }, []);

  useEffect(() => {
    billingDetailsIsValidHandler({ formObject: billingDetailInputs });
  }, [billingDetailInputs, billingDetailsIsValidHandler]);

  const billingDetailInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setBillingDetailInputs(updatedForm);
  };

  const billingDetailInputsHandler = async (event) => {
    event.preventDefault();

    setSnackbarIsOpen(true);

    // const billingDetails = {
    //   name: event.target.name.value,
    //   email: event.target.email.value,
    //   address: {
    //     city: event.target.city.value,
    //     line1: event.target.address.value,
    //     state: event.target.state.value,
    //     postal_code: event.target.zip.value,
    //   },
    // };

    // setIsProcessing(true);

    // const { data: clientSecret } = await axios.post('/api/payment_intents', {
    //   publicCard: 0,
    //   privateCard: 0,
    //   unlistedCard: 0,
    // });

    // const cardElement = elements.getElement(CardElement);

    // const paymentMethodReq = await stripe.createPaymentMethod({
    //   type: 'card',
    //   card: cardElement,
    //   billing_details: billingDetails,
    // });

    // const confirmedCardPayment = await stripe.confirmCardPayment(clientSecret, {
    //   payment_method: paymentMethodReq.paymentMethod.id,
    // });
  };

  const snackbarCloseHandler = () => {
    setSnackbarIsOpen(false);
  };

  if (!state.isAuthenticated) {
    return <Redirect to="/signin" />;
  }

  return (
    <>
      <PaperContainer>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <RestructureFormObject
            formObject={billingDetailInputs}
            formChangeHandler={billingDetailInputsChangeHandler}
          />
          <Paper className={classes.CardElementContainer}>
            <CardElement
              options={{
                style: {
                  base: {
                    fontSize: '16px',
                    color: theme.palette.text.primary,
                    '::placeholder': {
                      color: theme.palette.text.primary,
                    },
                    iconColor: theme.palette.secondary.main,
                  },
                  invalid: {
                    color: theme.palette.error.main,
                    iconColor: theme.palette.error.main,
                  },
                  complete: {
                    iconColor: theme.palette.secondary.main,
                  },
                },
                hidePostalCode: true,
                hideIcon: false,
                iconStyle: 'solid',
              }}
            />
          </Paper>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={shoppingIcon.path} />
              </SvgIcon>
            }
            disabled={!billingDetailInputsIsValid}
            color="secondary"
            onClick={billingDetailInputsHandler}
            pendingPosition="start"
            variant="contained"
            pending={isProcessing}
          >
            {`Checkout`}
          </LoadingButton>
        </Box>
      </PaperContainer>
      <SnackbarAlert
        isOpen={snackbarIsOpen}
        closeHandler={snackbarCloseHandler}
        severity={'info'}
      >
        The checkout request wil be sent to authentic server in production, this
        is only for demo purposes.
      </SnackbarAlert>
    </>
  );
};

export default Checkout;
