import React, { useState, useCallback, useEffect } from 'react';
import { SvgIcon, Grid, Typography } from '@material-ui/core';
import LoadingButton from '@material-ui/lab/LoadingButton';
import { makeStyles } from '@material-ui/core/styles';
import { Link as RouterLink } from 'react-router-dom';

import {
  closeCircleIcon,
  visibilityOffIcon,
  visibilityOnIcon,
  emailSendIcon,
  swapIcon,
} from '../../assets/svg/Common';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import { fieldsIsValidHandler } from '../helper/fieldsIsValidHandler';
import RestructureFormObject from '../HOC/RestructureFormObject';
import PaperContainer from '../HOC/PaperContainer';
import SnackbarAlert from '../HOC/SnackbarAlert';

const useStyles = makeStyles((theme) => ({
  buttonSpacing: {
    marginTop: theme.spacing(1),
  },
}));

const SignUp = (props) => {
  const classes = useStyles();

  const [isSignIn, setIsSignIn] = useState(true);
  const [isPendingSignIn, setIsPendingSignIn] = useState(false);
  const [isPendingRequestPassword, setIsPendingRequestPassword] = useState(
    false
  );
  const [isPendingChangeType, setIsPendingChangeType] = useState(false);
  const [passwordType, setPasswordType] = useState('password');
  const [toggleVisibilityIcon, setToggleVisibilityIcon] = useState(false);
  const [snackbarIsOpen, setSnackbarIsOpen] = useState(false);

  const [accountInputs, setAccountInputs] = useState({
    email: {
      value: '',
      name: 'email',
      label: 'Email',
      type: 'email',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    password: {
      value: '',
      name: 'password',
      label: 'Password',
      type: 'password',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: false, label: '*Required' },
      icon: {
        type: 'toggle',
        on: visibilityOnIcon,
        off: visibilityOffIcon,
        isOn: false,
      },
    },
  });

  const [accountInputsIsValid, setAccountInputsIsValid] = useState(
    false
  );

  const signUpHandler = () => {
    setSnackbarIsOpen(true);
  };

  const accountInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setAccountInputs(updatedForm);
  };

  const accountInputsIsValidHandler = useCallback((args) => {
    const fieldsIsValid = fieldsIsValidHandler(args);

    setAccountInputsIsValid(fieldsIsValid);
  }, []);

  useEffect(() => {
    accountInputsIsValidHandler({ formObject: accountInputs });
  }, [accountInputs, accountInputsIsValidHandler]);

  const snackbarCloseHandler = () => {
    setSnackbarIsOpen(false);
  };

  return (
    <PaperContainer>
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
      >
        <Typography variant="h4">Create Free Account</Typography>
        <Grid item>
          <RestructureFormObject
            formObject={accountInputs}
            formChangeHandler={accountInputsChangeHandler}
          />
        </Grid>

        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={emailSendIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            onClick={signUpHandler}
            pending={isPendingSignIn}
            disabled={!accountInputsIsValid}
            pendingPosition="start"
            variant="contained"
            className={classes.buttonSpacing}
            type="submit"
          >
            Register
          </LoadingButton>
        </Grid>
        <Grid item style={{ display: 'flex' }}>
          <LoadingButton
            component={RouterLink}
            to={'/signin'}
            startIcon={
              <SvgIcon>
                <path d={swapIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            pending={isPendingChangeType}
            pendingPosition="start"
            variant="outlined"
            className={classes.buttonSpacing}
          >
            Switch to Sign In
          </LoadingButton>
        </Grid>
      </Grid>
      <SnackbarAlert
        isOpen={snackbarIsOpen}
        closeHandler={snackbarCloseHandler}
        severity={'info'}
      >
        The account registration request wil be sent to authentic server in
        production, this is only for demo purposes.
      </SnackbarAlert>
    </PaperContainer>
  );
};

export default SignUp;
