import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
  Box,
  Avatar,
  SvgIcon,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { LoadingButton } from '@material-ui/lab';
import { Redirect } from 'react-router-dom';

import {
  closeCircleIcon,
  visibilityOffIcon,
  visibilityOnIcon,
  uploadIcon,
  pencilIcon,
  saveIcon,
  trashIcon,
  formTextboxPasswordIcon,
  sendIcon,
  warningDecagramIcon,
} from '../../assets/svg/Common';
import { useStore } from '../../hooks/store';
import RestructureFormObject from '../HOC/RestructureFormObject';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import PaperContainer from '../HOC/PaperContainer';
import SnackbarAlert from '../HOC/SnackbarAlert';
import ResponsiveDialog from '../HOC/ResponsiveDialog';

const useStyles = makeStyles((theme) => ({
  avatarIcon: {
    width: theme.spacing(24),
    height: theme.spacing(24),
    border: '1px red solid',
  },
  contentItem: {
    margin: theme.spacing(1),
  },
}));

const Details = (props) => {
  const [state, dispatch] = useStore(true);
  const [revalidateInputs, setRevalidateInputs] = useState({
    password: {
      value: '',
      name: 're-enter-password',
      label: 'Re-enter Password',
      type: 'password',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: false,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'toggle',
        on: visibilityOnIcon,
        off: visibilityOffIcon,
        isOn: false,
      },
    },
  });
  const [userDetailInputs, setUserDetailInputs] = useState({
    email: {
      value: state.user.email,
      name: 'email',
      label: 'Email',
      type: 'email',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: true,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    name: {
      value: state.user.name,
      name: 'name',
      label: 'Name',
      type: 'text',
      dataType: 'string',
      placeHolder: '',
      maxLength: 255,
      variant: 'outlined',
      multiline: false,
      disabled: true,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeCircleIcon,
      },
    },
    // image: {
    //   value: '',
    //   name: 'image',
    //   label: 'Image',
    //   type: 'text',
    //   placeHolder: '',
    //   multiline: false,
    //   validations: {
    //     isRequired: true,
    //   },
    //   isTouched: false,
    //   validity: { isValid: false, label: '*Required' },
    // },
  });
  const [profilePicture, setProfilePicture] = useState();
  const [profileIsLoading, setProfileIsLoading] = useState(false);
  const [previewProfile, setPreviewProfile] = useState();
  const [profileIsValid, setProfileIsValid] = useState(true);
  const [profileButtonIsEnabled, setProfileButtonIsEnabled] = useState(false);
  const [profileState, setProfileState] = useState('read');
  const [userDetailInputsIsValid, setUserDetailInputsIsValid] = useState(true);
  const [saveSnackbarIsOpen, setSaveSnackbarIsOpen] = useState(false);
  const [preDeleteSnackbarIsOpen, setPreDeleteSnackbarIsOpen] = useState(false);
  const [postDeleteSnackbarIsOpen, setPostDeleteSnackbarIsOpen] = useState(
    false
  );
  const [revalidateSnackbarIsOpen, setRevalidateSnackbarIsOpen] = useState(
    false
  );
  const [isRevalidated, setIsRevalidated] = useState(false);
  const [revalidateDialogIsOpen, setRevalidateDialogIsOpen] = useState(false);
  const [deleteDialogIsOpen, setDeleteDialogIsOpen] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    if (!profilePicture) {
      return;
    }
    const fileReader = new FileReader();
    fileReader.onload = () => {
      setPreviewProfile(fileReader.result);
    };
    fileReader.readAsDataURL(profilePicture);

    // setProfileIsLoading(false);
  }, [profilePicture]);

  const profileRef = useRef();

  const profileFormCompleteChangeHandler = useCallback((args) => {
    const { formObject } = { ...args };

    const updatedForm = { ...formObject };

    const formValidityList = [];

    for (let key in updatedForm) {
      formValidityList.push(updatedForm[key].validity.isValid);
    }

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setUserDetailInputsIsValid(formIsValid);
  }, []);

  useEffect(() => {
    profileFormCompleteChangeHandler({ formObject: userDetailInputs });
  }, [userDetailInputs, profileFormCompleteChangeHandler]);

  const userDetailInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setUserDetailInputs(updatedForm);
  };

  const revalidateInputsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setRevalidateInputs(updatedForm);
  };

  const profileChangeHandler = (event) => {
    let pickedProfile;

    if (event.target.files && event.target.files.length === 1) {
      pickedProfile = event.target.files[0];
      setProfilePicture(pickedProfile);
      setProfileIsValid(true);
    } else {
      setProfileIsValid(false);
    }
  };

  const pickProfileHandler = () => {
    // setProfileIsLoading(true);
    profileRef.current.click();
  };

  const editProfileHandler = () => {
    const copiedUser = JSON.parse(JSON.stringify(userDetailInputs));

    for (const key in copiedUser) {
      copiedUser[key].disabled = !copiedUser[key].disabled;
    }

    setProfileButtonIsEnabled(!profileButtonIsEnabled);
    setUserDetailInputs(copiedUser);
    setProfileState(profileState === 'read' ? 'update' : 'read');
  };

  const saveProfileHandler = () => {
    const copiedUser = JSON.parse(JSON.stringify(userDetailInputs));

    for (const key in copiedUser) {
      copiedUser[key].disabled = !copiedUser[key].disabled;
    }

    setProfileButtonIsEnabled(!profileButtonIsEnabled);
    setUserDetailInputs(copiedUser);
    setProfileState(profileState === 'read' ? 'update' : 'read');
    setSaveSnackbarIsOpen(true);
  };

  const saveSnackbarCloseHandler = () => {
    setSaveSnackbarIsOpen(false);
  };

  const revalidateHandler = () => {
    if (revalidateInputs.password.value === state.user.password) {
      setUserDetailInputs((prev) => {
        return {
          ...prev,
          password: {
            value: state.user.password,
            name: 'password',
            label: 'Password',
            type: 'password',
            dataType: 'string',
            placeHolder: '',
            maxLength: 255,
            variant: 'outlined',
            multiline: false,
            disabled: true,
            validations: {
              isRequired: true,
            },
            isTouched: false,
            validity: { isValid: true, label: '*Required' },
            icon: {
              type: 'toggle',
              on: visibilityOnIcon,
              off: visibilityOffIcon,
              isOn: false,
            },
          },
        };
      });
      setIsRevalidated(true);
    } else {
      setRevalidateSnackbarIsOpen(true);
    }
    setRevalidateDialogIsOpen(false);
  };

  const showPasswordHandler = () => {
    setRevalidateDialogIsOpen(true);
  };

  const showDeleteAccountHandler = () => {
    state.user.cards.length > 0
      ? setPreDeleteSnackbarIsOpen(true)
      : setDeleteDialogIsOpen(true);
  };

  const deleteAccountHandler = () => {
    setPostDeleteSnackbarIsOpen(true);
    setDeleteDialogIsOpen(false);
  };

  const deleteDialogCloseHandler = () => {
    setDeleteDialogIsOpen(false);
  };

  const preDeleteSnackbarCloseHandler = () => {
    setPreDeleteSnackbarIsOpen(false);
  };

  const postDeleteSnackbarCloseHandler = () => {
    setPostDeleteSnackbarIsOpen(false);
  };

  const revalidateDialogCloseHandler = () => {
    setRevalidateDialogIsOpen(false);
  };

  const revalidateSnackbarCloseHandler = () => {
    setRevalidateSnackbarIsOpen(false);
  };

  if (!state.isAuthenticated) {
    return <Redirect to="/signin" />;
  }

  return (
    <PaperContainer>
      <Box
        sx={{
          display: 'grid',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <Box sx={{ display: 'none' }}>
          <input
            id="profile-picture"
            ref={profileRef}
            type="file"
            accept=".jpg,.png,.jpeg"
            onChange={profileChangeHandler}
          />
        </Box>
        <Box
          sx={{ display: 'flex', justifyContent: 'center' }}
          className={classes.contentItem}
        >
          <Avatar className={classes.avatarIcon} src={state.user.image} />
        </Box>
        <Box
          sx={{ display: 'flex', justifyContent: 'center' }}
          className={classes.contentItem}
        >
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={uploadIcon.path} />
              </SvgIcon>
            }
            disabled={!profileButtonIsEnabled}
            color="secondary"
            onClick={pickProfileHandler}
            pendingPosition="start"
            variant="outlined"
            pending={profileIsLoading}
          >
            Upload Profile
          </LoadingButton>
        </Box>
        <RestructureFormObject
          formObject={userDetailInputs}
          formChangeHandler={userDetailInputsChangeHandler}
        />
        {profileState === 'read' ? (
          <>
            {!isRevalidated ? (
              <Box
                sx={{ display: 'flex', justifyContent: 'center' }}
                className={classes.contentItem}
              >
                <LoadingButton
                  startIcon={
                    <SvgIcon>
                      <path d={formTextboxPasswordIcon.path} />
                    </SvgIcon>
                  }
                  disabled={!userDetailInputsIsValid}
                  color="secondary"
                  onClick={showPasswordHandler}
                  pendingPosition="start"
                  variant="contained"
                  // pending={profileIsLoading}
                >
                  Show Password
                </LoadingButton>
                <ResponsiveDialog
                  isOpen={revalidateDialogIsOpen}
                  closeHandler={revalidateDialogCloseHandler}
                  svgPath={formTextboxPasswordIcon.path}
                  title={'Revalidate Password'}
                  shouldAuthenticated={true}
                >
                  <>
                    <DialogContent>
                      <DialogContentText>
                        Re-enter password before showing password field
                      </DialogContentText>
                      <Box sx={{ display: 'grid', justifyContent: 'center' }}>
                        <RestructureFormObject
                          formObject={revalidateInputs}
                          formChangeHandler={revalidateInputsChangeHandler}
                        />
                      </Box>
                    </DialogContent>
                    <DialogActions>
                      <LoadingButton
                        startIcon={
                          <SvgIcon>
                            <path d={sendIcon.path} />
                          </SvgIcon>
                        }
                        color="secondary"
                        onClick={revalidateHandler}
                        pendingPosition="start"
                        variant="contained"
                        // pending={profileIsLoading}
                      >
                        confirm
                      </LoadingButton>
                    </DialogActions>
                  </>
                </ResponsiveDialog>
              </Box>
            ) : (
              <Box
                sx={{ display: 'flex', justifyContent: 'center' }}
                className={classes.contentItem}
              >
                <LoadingButton
                  startIcon={
                    <SvgIcon>
                      <path d={trashIcon.path} />
                    </SvgIcon>
                  }
                  color="secondary"
                  onClick={showDeleteAccountHandler}
                  pendingPosition="start"
                  variant="contained"
                  // pending={profileIsLoading}
                >
                  Delete Account
                </LoadingButton>
                <ResponsiveDialog
                  isOpen={deleteDialogIsOpen}
                  closeHandler={deleteDialogCloseHandler}
                  svgPath={warningDecagramIcon.path}
                  title={'Delete Account'}
                  shouldAuthenticated={true}
                >
                  <>
                    <DialogContent>
                      <DialogContentText>
                        Delete account cannot be reverted, are you sure you want
                        to delete the account?
                      </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                      <LoadingButton
                        startIcon={
                          <SvgIcon>
                            <path d={trashIcon.path} />
                          </SvgIcon>
                        }
                        color="secondary"
                        onClick={deleteAccountHandler}
                        pendingPosition="start"
                        variant="contained"
                        // pending={profileIsLoading}
                      >
                        Yes
                      </LoadingButton>
                    </DialogActions>
                  </>
                </ResponsiveDialog>
              </Box>
            )}
            <Box
              sx={{ display: 'grid', justifyContent: 'center' }}
              className={classes.contentItem}
            >
              <LoadingButton
                startIcon={
                  <SvgIcon>
                    <path d={pencilIcon.path} />
                  </SvgIcon>
                }
                color="secondary"
                onClick={editProfileHandler}
                pendingPosition="start"
                variant="contained"
                // pending={profileIsLoading}
              >
                Edit
              </LoadingButton>
            </Box>
          </>
        ) : (
          <Box
            sx={{ display: 'grid', justifyContent: 'center' }}
            className={classes.contentItem}
          >
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={saveIcon.path} />
                </SvgIcon>
              }
              disabled={!userDetailInputsIsValid}
              color="secondary"
              onClick={saveProfileHandler}
              pendingPosition="start"
              variant="contained"
              // pending={profileIsLoading}
            >
              Save
            </LoadingButton>
          </Box>
        )}
      </Box>
      <SnackbarAlert
        isOpen={saveSnackbarIsOpen}
        closeHandler={saveSnackbarCloseHandler}
        severity={'info'}
      >
        The save request wil be sent to authentic server in production, this is
        only for demo purposes.
      </SnackbarAlert>
      <SnackbarAlert
        isOpen={revalidateSnackbarIsOpen}
        closeHandler={revalidateSnackbarCloseHandler}
        severity={'error'}
      >
        Wrong password provided, revalidation failed.
      </SnackbarAlert>
      <SnackbarAlert
        isOpen={preDeleteSnackbarIsOpen}
        closeHandler={preDeleteSnackbarCloseHandler}
        severity={'error'}
      >
        Please remove all cards before deleting the account.
      </SnackbarAlert>
      <SnackbarAlert
        isOpen={postDeleteSnackbarIsOpen}
        closeHandler={postDeleteSnackbarCloseHandler}
        severity={'info'}
      >
        The delete request wil be sent to authentic server in production, this
        is only for demo purposes.
      </SnackbarAlert>
    </PaperContainer>
  );
};

export default Details;
