import React, { useMemo, useState } from 'react';
import { SvgIcon, Box } from '@material-ui/core';
import { LoadingButton } from '@material-ui/lab';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Link as RouterLink } from 'react-router-dom';

import { plusIcon, shoppingCartIcon } from '../../assets/svg/Common';
import CardList from '../Card/CardList';
import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({}));

const TabCardContent = (props) => {
  const { cardData, cardType } = props;
  const [state, dispatch] = useStore();
  const theme = useTheme();
  const classes = useStyles();
  const [availableCardsCount, setAvailableCardsCount] = useState(0);
  const [cardsLimitCount, setCardsLimitCount] = useState(0);
  const [isOutOfLimit, setIsOutOfLimit] = useState(false);

  useMemo(() => {
    let initialValue = 0;

    const availableCardsTotalCount = cardData.reduce(
      (accumulator, currentValue) => {
        return currentValue.cardType === cardType
          ? accumulator + 1
          : accumulator;
      },
      initialValue
    );

    const cardsLimitTotalCount =
      cardType === 'public'
        ? state.user.cardsLimit.public
        : cardType === 'unlisted'
        ? state.user.cardsLimit.unlisted
        : state.user.cardsLimit.private;

    const cardsIsOutOfLimit =
      availableCardsTotalCount < cardsLimitTotalCount ? false : true;

    setAvailableCardsCount(availableCardsTotalCount);
    setCardsLimitCount(cardsLimitTotalCount);
    setIsOutOfLimit(cardsIsOutOfLimit);
  }, [
    cardData,
    cardType,
    state.user.cardsLimit.public,
    state.user.cardsLimit.unlisted,
    state.user.cardsLimit.private,
  ]);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column' }}>
      <Box
        sx={{
          display: 'flex',
          [theme.breakpoints.down('sm')]: {
            flexDirection: 'column',
            justifyContent: 'center',
          },
          [theme.breakpoints.up('sm')]: {
            flexDirection: 'row',
            justifyContent: 'space-between',
          },
        }}
      >
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            [theme.breakpoints.down('sm')]: {
              margin: 1,
            },
            [theme.breakpoints.up('sm')]: {
              margin: `0 ${theme.spacing(2)}`,
            },
          }}
        >
          <LoadingButton
            component={RouterLink}
            to={`/cart`}
            startIcon={
              <SvgIcon>
                <path d={shoppingCartIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            pendingPosition="start"
            variant="contained"
            type="submit"
          >
            Purchase More Cards
          </LoadingButton>
        </Box>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            [theme.breakpoints.down('sm')]: {
              margin: 1,
            },
            [theme.breakpoints.up('sm')]: {
              margin: `0 ${theme.spacing(2)}`,
            },
          }}
        >
          <LoadingButton
            component={RouterLink}
            to={`/card-details`}
            startIcon={
              <SvgIcon>
                <path d={plusIcon.path} />
              </SvgIcon>
            }
            color="secondary"
            pendingPosition="start"
            variant="contained"
            disabled={isOutOfLimit}
            type="submit"
          >
            {`Create ${cardType} Card ${availableCardsCount}/${cardsLimitCount}`}
          </LoadingButton>
        </Box>
      </Box>
      <Box>
        <CardList
          cardDataList={cardData.filter(
            (card) =>
              card.cardType === cardType && card.users._id === state.user._id
          )}
        />
      </Box>
    </Box>
  );
};

export default TabCardContent;
